@import url(https://fonts.googleapis.com/css?family=Coming+Soon);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  text-align: center;
  height: 100vh;
  display: flex;
  align-items: center;
  background: url("https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/intermediary/f/61f7685f-47e5-49a0-b5cb-ca6b8cab4228/drok89-a7a2a550-ba87-40a6-9701-b635550d55b3.jpg");
  justify-content: center;
  background-position: center center;
  font-family: "Coming Soon", cursive;
}


.buttonWord {
    width: 30%;
    display:inline-block;
}

.image{
    margin-left: 113px;
}

.word{
    margin: 0.4em -1em 0.2em 0;
    font-size: 2rem;
    letter-spacing: 1em;
}
.lose{
    font-weight: bold;
    color: red;
}
.resetBtn button{
    width: 50px;
    height: 40px;
    padding: 5px;
    margin: 5px 3px 3px 3px;
    border-radius: 5px;
    background-color: #dcdde1;
    box-shadow: 0 6px #c8d6e5;
    font-family: inherit;
}

.buttonWord button{
    width: 30px;
    height: 30px;
    padding: 5px;
    margin: 5px 3px 3px 3px;
    border-radius: 5px;
    background-color: #dcdde1;
    box-shadow: 0 6px #c8d6e5;
    font-family: inherit;
}

h2{
    color:#009432;
}
